import react from "react";
import "./Configs.scss";
import swal from 'sweetalert';

export default class Check extends react.Component {
    constructor(props) {
        super(props);
        this.state = {
            advice: "",
            treatmentType: "1",
            healthStatus: "1",
            diagnosis: "1",
            delayRequest: "500",
            delayPost: "10000"
        }
    }

    handleChange(field, e) {
        let fields = this.state;
        fields[field] = e.target.value;
        this.setState({ fields });
    }

    componentDidMount() {
        this.setState({
            advice: localStorage.getItem("advice") || "",
            treatmentType: localStorage.getItem("treatment-type") || "1",
            healthStatus: localStorage.getItem("health-status") || "1",
            diagnosis: localStorage.getItem("diagnosis") || "1",
            delayRequest: localStorage.getItem("delay-request") || "500",
            delayPost: localStorage.getItem("delay-post") || "10000"
        });
    }
    saveToLocal(data) {
        for (let key in data) {
            localStorage.setItem(key, data[key]);
        }
    }

    async saveData() {
        this.saveToLocal({
            "advice": this.state.advice,
            "treatment-type": this.state.treatmentType,
            "health-status": this.state.healthStatus,
            "diagnosis": this.state.diagnosis,
            "delay-request": this.state.delayRequest,
            "delay-post": this.state.delayPost
        });
        swal("Success", "Save successfully!", "success");
    }

    render() {
        return (
            <div className="af-configs-page af-full-page" >
                <div className="page-name">
                    <h1>Cu hnh</h1>
                    <p>
                        Thit lp cc gi tr dng  kim d liu v to d liu t ng
                    </p>
                </div>

                <div className="page">
                    <div
                        className="row"
                        name="config-form">
                        <div className="col-md-6 p-5 section">
                            <div className="form-group">
                                <label htmlFor="config-advice">Li dn</label>
                                <textarea
                                    rows={4}
                                    className="form-control"
                                    id="config-advice"
                                    placeholder="Advice"
                                    value={this.state.advice}
                                    onChange={this.handleChange.bind(this, "advice")} />
                            </div>
                            <div className="form-group row">
                                <label htmlFor="config-treatment-type" className="col-6 col-form-label">Loi x l</label>
                                <div className="col-6">
                                    <select
                                        className="form-control"
                                        id="config-treatment-type"
                                        value={this.state.treatmentType}
                                        onChange={this.handleChange.bind(this, "treatmentType")}>
                                        <option value="1">T vn</option>
                                        <option value="2">Khm ti nh</option>
                                        <option value="3">Khm ti y t</option>
                                        <option value="4">Cp cu</option>
                                    </select>
                                </div>
                            </div>
                            <div className="form-group row">
                                <label htmlFor="config-health-status" className="col-6 col-form-label">Tnh trng sc khe</label>
                                <div className="col-6">
                                    <select
                                        className="form-control"
                                        id="config-treatment-type"
                                        value={this.state.healthStatus}
                                        onChange={this.handleChange.bind(this, "healthStatus")}>
                                        <option value="1">Khng n nh</option>
                                        <option value="2">n nh</option>
                                        <option value="3">Khc</option>
                                        <option value="4">T vong</option>
                                    </select>
                                </div>
                            </div>
                            <div className="form-group row">
                                <label htmlFor="config-health-status" className="col-6 col-form-label">Chn on</label>
                                <div className="col-6">
                                    <select
                                        className="form-control"
                                        id="config-treatment-type"
                                        value={this.state.diagnosis}
                                        onChange={this.handleChange.bind(this, "diagnosis")}>
                                        <option value="1">Khng triu chng</option>
                                        <option value="2">Mc  nh</option>
                                        <option value="3">Mc  trung bnh</option>
                                        <option value="4">Mc  nng</option>
                                        <option value="5">Mc  nguy kch</option>
                                        <option value="6">T vong</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6 p-5 section">
                            <div className="form-group row">
                                <label htmlFor="config-delay-request" className="col-6 col-form-label"> tr gia cc request</label>
                                <div className="col-6">
                                    <input
                                        type="number"
                                        className="form-control"
                                        id="config-delay-request"
                                        placeholder=" tr"
                                        value={this.state.delayRequest}
                                        onChange={this.handleChange.bind(this, "delayRequest")} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label htmlFor="config-delay-post" className="col-6 col-form-label"> tr gia cc ln to</label>
                                <div className="col-6">
                                    <input
                                        type="number"
                                        className="form-control"
                                        id="config-delay-post"
                                        placeholder=" tr"
                                        value={this.state.delayPost}
                                        onChange={this.handleChange.bind(this, "delayPost")} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex justify-content-end mt-5">
                        <button className="btn btn-primary" onClick={this.saveData.bind(this)}>Lu</button>
                    </div>
                </div>
            </div>
        )
    }
}